<template>
  <div>
  	<div class="login">
  		<el-tabs class="login-wrap" v-model="activeName">
    		<el-tab-pane class="login-content" label="登录" name="first">
    			<el-form :model="loginForm" ref="loginForm" label-width="100px" class="demo-dynamic">
          <el-form-item label="用户名" prop="name" :rules="[
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ]">
      <el-input v-model="loginForm.name"></el-input>
    </el-form-item>
					<el-form-item label="密码" prop="pass" :rules="[
            { validator: validatePass, trigger: 'blur' },
            { required: true, message: '请输入密码', trigger: 'blur' }
          ]">
    	<el-input type="password" v-model="loginForm.pass" auto-complete="off"></el-input>
  			</el-form-item>
  		<el-form-item label-width="100px">
    		<el-button type="primary" @click="loginSubmit(loginForm)">登录</el-button>
    		<el-button @click="resetForm('loginForm')">重置</el-button>
  			</el-form-item>
		</el-form>
    		</el-tab-pane>
       


    		<el-tab-pane class="login-content" label="注册" name="second">
    			<el-form :model="resgiterForm" ref="resgiterForm" label-width="100px" class="demo-dynamic">
          <el-form-item label="用户名" prop="name" :rules="[
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ]">
      <el-input v-model="resgiterForm.name"></el-input>
    </el-form-item>
					<el-form-item label="密码" prop="pass" :rules="[
            { validator: validatePass, trigger: 'blur' },
            { required: true, message: '请输入密码', trigger: 'blur' }
          ]">
    	<el-input type="password" v-model="resgiterForm.pass" auto-complete="off"></el-input>
  			</el-form-item>
  		<el-form-item prop="email" label="邮箱" :rules="[
      { required: true, message: '请输入邮箱地址', trigger: 'blur' },
      { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur,change' }]">
   			 <el-input v-model="resgiterForm.email"></el-input>
  		</el-form-item>
  		<el-form-item label-width="100px">
    		<el-button type="primary" @click="resgiterSubmit(resgiterForm)">注册</el-button>
    		<el-button @click="resetForm('resgiterForm')">重置</el-button>
  			</el-form-item>
		</el-form>
    		</el-tab-pane>
  		</el-tabs>
  	</div>
  </div>
</template>

