	export default {
    data() {
      return {
        //一开始显示登录
        activeName: 'first',
        //注册
        loginForm: {
          name:'',
          pass:''
        },
        resgiterForm: {
          email: '',
          name:'',
          pass:''
        }
      };
    },
   methods: {
      loginSubmit() {
      //jq.ajax({
    //   url: "http:localhost:8088/user/lo",
    //   method: "GET",
    //   // data: {},
    //   success: function(data) {
    //     if(data) {
    //       alert(11)
    //       console.log(data);
    //     } else {
    //       // phone.css({"border",""})
    //       alert("该号码已被注册");
    //     }
    //   }
    // });
        this.$axios.get('http:localhost:8088/user/lo'
          )
        .then((res)=>{
            console.log(res);
        })
        .catch(function(res) {
          console.log(res);
        });
      },
      resgiterSubmit(formName) {
          
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }

  };
</script>



